import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useClsCatalogStudentRollEnter } from "../../../Hooks/ApiHooks/ClassManager/class-manager-api";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import CreateButton from "../../../Ui/Button/CreateButton";
import {
  assestsFeedUrl,
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import style from "../../PlateformUser/PlateformUser.module.css";
import styleCard from "../../../../MembersTab/staff/Mainbody/StaffAdmisionAdmin/AdmissionScholarship/AdmissionScholarship.module.css";
import FormInput from "../../../Ui/Input/FormInput";
import QvipleLoader from "../../../Loader/QvipleLoader";
const AssignStudentRollNumber = ({ onClose, onRefetch, students, cid }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [allStudent, setAllStudent] = useState([]);
  const [searchCopy, setSearchCopy] = useState("");
  const [state, setState] = useState(null);
  const [clsCatalogStudentRollEnter] = useClsCatalogStudentRollEnter();

  useEffect(() => {
    if (students?.length) {
      setAllStudent(students);
    }
  }, [students]);

  const onSearchEvent = (e) => {
    if (students?.length >= 0) {
      setSearchCopy(e.target.value);
    }
  };

  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      if (searchCopy) {
        const filterStudent = students?.filter((student) => {
          if (
            `${student?.studentFirstName} ${
              student?.studentMiddleName ? student?.studentMiddleName + " " : ""
            }${student?.studentLastName}`
              ?.toLowerCase()
              ?.includes(searchCopy?.toLowerCase())
          ) {
            return student;
          } else {
            return null;
          }
        });
        setAllStudent(filterStudent);
      } else {
        setAllStudent(students);
      }
    }
  };

  const onChangeRoll = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onSaveRoll = () => {
    if (cid && state) {
      setDisabled((pre) => !pre);
      let arr = [];
      for (let obj in state) {
        let dt = state[obj];
        arr.push({
          student: obj,
          roll: dt,
        });
      }
      clsCatalogStudentRollEnter({
        cid: cid,
        bodyParameter: {
          student_list: arr,
        },
      })
        .then(() => {
          onRefetch();
          onClose();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  return (
    <>
      <Modal onClose={() => {}}>
        <div
          className={style.modal_container}
          style={{
            minWidth: "45rem",
            maxWidth: "45rem",
          }}
        >
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("students")}</h6>
              </div>
              <div className={style.modal_container_btn_container}>
                <CreateButton
                  label={t("save")}
                  onAction={onSaveRoll}
                  customStyle={{
                    margin: "0",
                    padding: "0.3rem 0.5rem",
                  }}
                />
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingBottom: "1rem",
            }}
          >
            <div
              className={style.assign_search_container}
              style={{
                marginBottom: "1rem",
              }}
            >
              <input
                type="text"
                placeholder={t("search")}
                className={style.assign_search_input}
                onChange={onSearchEvent}
                onKeyUp={onCallBindSearch}
                value={searchCopy}
                style={{
                  width: "100%",
                }}
              />
              <img
                src={`${assestsNavbarUrl}/navbar-search.svg`}
                alt="search icon"
              />
            </div>
            {allStudent?.map((student) => (
              <section
                className={`${styleCard.student_container}`}
                key={student?._id}
                style={{
                  padding: "0.6rem 0",
                  margin: "0",
                  justifyContent: "space-between",
                }}
              >
                <div
                  className={`${styleCard.student_container}`}
                  style={{
                    padding: 0,
                    margin: 0,
                    borderBottom: "none",
                  }}
                >
                  <img
                    src={
                      student?.studentProfilePhoto
                        ? `${imageShowUrl}/${student?.studentProfilePhoto}`
                        : `${assestsFeedUrl}/feed-user-avatar.svg`
                    }
                    alt="student avatar"
                  />
                  <div className={styleCard.student_container_name}>
                    <h6>{`${student?.studentFirstName} ${
                      student?.studentMiddleName
                        ? student?.studentMiddleName
                        : ""
                    } ${student?.studentLastName}`}</h6>
                    <p className={styleCard.student_container_gr}>
                      {t("gr_number")} {student?.studentGRNO ?? ""}
                    </p>
                  </div>
                </div>

                <FormInput
                  label=""
                  name={student?._id}
                  type="text"
                  value={
                    state && state[student?._id] && state[student?._id]["roll"]
                  }
                  onChange={onChangeRoll}
                />
              </section>
            ))}
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default AssignStudentRollNumber;
