import { useState } from "react";
import PopupWrapper from "../../../../MembersTab/staff/Mainbody/FinanceManager/FeesStructure/PopupWrapper";
import style from "../../../../MembersTab/staff/Mainbody/StaffAdmisionAdmin/MiddleContent/Funds/Funds.module.css";
import QLoader from "../../../../Loader/QLoader";
import { useTranslation } from "react-i18next";
import BorderBottom from "../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import { useExportStudentAttendanceZip } from "../DepartmentApi/depart-api";
import useCalendar from "../../../../MembersTab/staff/Mainbody/FinanceManager/Payroll/Employee/useCalender";
import tileStyle from "./Tile.module.css";
import {
  pdfCdnUrl,
  daybookBaseUrl,
} from "../../../../optimized/Services/UrlConfig/BaseUrl";
import FormInput from "../../../../optimized/Ui/Input/FormInput";
import {
  existingRequiredField,
  requiredField,
} from "../../../../optimized/Validation/functions/checkingRequiredField";
import { slashDateFormat } from "../../../../optimized/Utils/Functions/dateUtility";
import FormCalendarInput from "../../../../optimized/Ui/Input/FormCalendarInput";
import Switch from "@mui/material/Switch";
import CustomGeneratedPdf from "../../../../optimized/Ui/GeneratedPdf/CustomGeneratedPdf";
import { useClsTheoryPracticalAttendanceReportExport } from "../../../../optimized/Hooks/ApiHooks/ClassManager/class-manager-api";
const label = { inputProps: { "aria-label": "Switch demo" } };

const ExportStudentAttendance = ({ onClose, cid }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [errorField, setErrorField] = useState("");
  const [isType, setIsType] = useState("");
  const [range, setRange] = useState({
    from: "",
    to: "",
    criteria: "",
    is_pdf: false,
  });
  const [openPdf, setOpenPdf] = useState("");

  const { daysInMonth, selectedDate, monthNames, getNextMonth, getPrevMonth } =
    useCalendar();
  const [exportStudentAttendanceZip] = useExportStudentAttendanceZip();
  const [clsTheoryPracticalAttendanceReportExport] =
    useClsTheoryPracticalAttendanceReportExport();

  const onNext = () => {
    getNextMonth();
  };
  const onPrev = () => {
    getPrevMonth();
  };
  const onType = (val) => {
    setIsType(val);
  };

  const onExportData = (val) => {
    let flag = true;
    if (["CUSTOM", "CUSTOM_MIXED", "ALL_SUBJECT_CUSTOM"]?.includes(isType)) {
      let validation = requiredField(range, ["criteria", "is_pdf"]);
      if (existingRequiredField(validation)) {
        flag = true;
      } else {
        flag = false;
        setErrorField(validation);
      }
    }
    if (flag) {
      setDisabled((pre) => !pre);
      let newDate = new Date(
        `${daysInMonth} ${
          monthNames[selectedDate.getMonth()]
        } ${selectedDate.getFullYear()}`
      );
      newDate = newDate.toISOString();
      if (["CUSTOM_MIXED", "ALL_MIXED"]?.includes(isType)) {
        clsTheoryPracticalAttendanceReportExport({
          cid: cid,
          is_type:
            isType === "CUSTOM_MIXED"
              ? "CUSTOM"
              : isType === "ALL_MIXED"
              ? "ALL"
              : "",
          which_type_list: val,
          criteria: range?.criteria,
          is_pdf: range.is_pdf ? "YES" : "NO",
          month: `${newDate.substring(5, 7)}`,
          year: `${newDate.substring(0, 4)}`,
          bodyParameter: range,
        })
          .then((res) => {
            if (res?.data?.excel_key) {
              setTimeout(() => {
                setDisabled((pre) => !pre);
                if (range.is_pdf) {
                  setOpenPdf(res?.data?.excel_key);
                } else {
                  const newWindow = window.open(
                    `${pdfCdnUrl}/${res?.data?.excel_key}`,
                    "_blank",
                    "noopener,noreferrer"
                  );
                  if (newWindow) newWindow.opener = null;
                  onClose();
                }
              }, 2500);
            } else {
              setDisabled((pre) => !pre);
            }
          })
          .catch(() => {});
      } else {
        exportStudentAttendanceZip({
          cid: cid,
          is_type: isType,
          which_type_list: val,
          criteria: range?.criteria,
          is_pdf: range.is_pdf ? "YES" : "NO",
          month: `${newDate.substring(5, 7)}`,
          year: `${newDate.substring(0, 4)}`,
          zipData: range,
        })
          .then((res) => {
            if (res?.data?.excel_key) {
              setTimeout(() => {
                setDisabled((pre) => !pre);
                if (range.is_pdf) {
                  setOpenPdf(res?.data?.excel_key);
                } else {
                  const newWindow = window.open(
                    `${pdfCdnUrl}/${res?.data?.excel_key}`,
                    "_blank",
                    "noopener,noreferrer"
                  );
                  if (newWindow) newWindow.opener = null;
                  onClose();
                }
              }, 2500);
            } else {
              setDisabled((pre) => !pre);
            }
          })
          .catch(() => {});
      }
    }
  };

  const onInputChange = (e) => {
    setRange((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };
  const onTransactionDate = (val, flow) => {
    setRange((prev) => ({
      ...prev,
      [flow]: slashDateFormat(val),
    }));
  };
  const onClosePdf = () => {
    setOpenPdf("");
  };

  const onTogglePdf = (e) => {
    setRange((prev) => ({
      ...prev,
      is_pdf: e.target.checked,
    }));
  };
  return (
    <>
      {openPdf ? (
        <CustomGeneratedPdf
          subUrl={daybookBaseUrl}
          documentUrl={openPdf}
          fileName={openPdf}
          onClose={onClosePdf}
        />
      ) : (
        <PopupWrapper onClose={onClose}>
          <div
            className={style.filter_container}
            style={{
              minHeight: "15rem",
              width: "55rem",
            }}
          >
            <h6 className={style.filter_title}>
              {t("filter_and_export_student_attendance")}
            </h6>
            <BorderBottom
              customStyle={{
                width: "100%",
              }}
            />

            <section
              className={tileStyle.tile_tile_container}
              style={{
                marginTop: "1rem",
                flexWrap: "wrap",
              }}
            >
              <div
                className={
                  isType === ""
                    ? tileStyle.tile_tile_active
                    : tileStyle.tile_tile
                }
                onClick={() => onType("")}
              >
                {t("monthly")}
              </div>
              <div
                className={
                  isType === "CUSTOM"
                    ? tileStyle.tile_tile_active
                    : tileStyle.tile_tile
                }
                onClick={() => onType("CUSTOM")}
              >
                {t("custom_date")}
              </div>

              <div
                className={
                  isType === "ALL"
                    ? tileStyle.tile_tile_active
                    : tileStyle.tile_tile
                }
                onClick={() => onType("ALL")}
              >
                {t("semester")}
              </div>
            </section>
            <section
              className={tileStyle.tile_tile_container}
              style={{
                marginTop: "1rem",
                flexWrap: "wrap",
              }}
            >
              <div
                className={
                  isType === "ALL_SUBJECT_MONTHLY"
                    ? tileStyle.tile_tile_active
                    : tileStyle.tile_tile
                }
                onClick={() => onType("ALL_SUBJECT_MONTHLY")}
              >
                {t("all_subject_monthly")}
              </div>
              <div
                className={
                  isType === "ALL_SUBJECT_CUSTOM"
                    ? tileStyle.tile_tile_active
                    : tileStyle.tile_tile
                }
                onClick={() => onType("ALL_SUBJECT_CUSTOM")}
              >
                {t("all_subject_custom_date")}
              </div>

              <div
                className={
                  isType === "ALL_SUBJECT_SEMESTER"
                    ? tileStyle.tile_tile_active
                    : tileStyle.tile_tile
                }
                onClick={() => onType("ALL_SUBJECT_SEMESTER")}
              >
                {t("all_subject_semester")}
              </div>
            </section>

            <section
              className={tileStyle.tile_tile_container}
              style={{
                marginTop: "1rem",
                flexWrap: "wrap",
              }}
            >
              <div
                className={
                  isType === "ALL_MIXED"
                    ? tileStyle.tile_tile_active
                    : tileStyle.tile_tile
                }
                onClick={() => onType("ALL_MIXED")}
              >
                {t("mixed")}
              </div>
              <div
                className={
                  isType === "CUSTOM_MIXED"
                    ? tileStyle.tile_tile_active
                    : tileStyle.tile_tile
                }
                onClick={() => onType("CUSTOM_MIXED")}
              >
                {t("mixed_custom_date")}
              </div>
            </section>
            {!isType || isType === "ALL_SUBJECT_MONTHLY" ? (
              <section className={style.fees_head_title}>
                <img
                  src="/images/members/back-icon-white.svg"
                  style={{
                    cursor: "pointer",
                  }}
                  onClick={onPrev}
                />

                <h6 className={`${style.yearmonth}`}>
                  {`${monthNames[selectedDate.getMonth()]}`}{" "}
                  {`${selectedDate.getFullYear()}`}
                </h6>

                <img
                  src="/images/members/forward-icon-white.svg"
                  style={{
                    cursor: "pointer",
                  }}
                  onClick={onNext}
                />
              </section>
            ) : isType === "ALL_SUBJECT_CUSTOM" ||
              isType === "CUSTOM" ||
              isType === "CUSTOM_MIXED" ? (
              <>
                <FormCalendarInput
                  label={t("from")}
                  placeholder={t("please_select_from_date")}
                  name={"from"}
                  value={range.from ?? ""}
                  onChange={(e) => onTransactionDate(e, "from")}
                  errorShow={errorField["from"]}
                />

                <FormCalendarInput
                  label={t("to_only")}
                  placeholder={t("please_select_to_date")}
                  name={"to"}
                  value={range.to ?? ""}
                  onChange={(e) => onTransactionDate(e, "to")}
                  errorShow={errorField["to"]}
                />
              </>
            ) : null}

            <FormInput
              label={`${t("defaulter_list_criteria")}`}
              placeholder={t("defaulter_list_criteria_placeholder")}
              name={"criteria"}
              value={range.criteria}
              type={"tel"}
              onChange={onInputChange}
            />
            <div
              style={{
                marginTop: "0.7rem",
              }}
            >
              <Switch
                {...label}
                checked={range.is_pdf}
                onChange={onTogglePdf}
              />
              <span>{t("report_in_pdf")}</span>
            </div>

            <div
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "space-between",
                gap: "1rem",
              }}
            >
              <button
                className={style.next_btn}
                onClick={() => onExportData("")}
                style={{
                  marginTop: "3rem",
                }}
              >
                {t("report_list_export_data")}
              </button>
              <button
                className={style.next_btn}
                onClick={() => onExportData("DEFAULTER")}
                style={{
                  marginTop: "3rem",
                }}
              >
                {t("defaulter_list_export_data")}
              </button>
            </div>
          </div>
          {disabled && <QLoader />}
        </PopupWrapper>
      )}
    </>
  );
};

export default ExportStudentAttendance;
